import React, { Component } from 'react'
import styles from './index.scss'
import menu from '../../static/images/menu.svg'
import down from '../../static/images/down.svg'
import play from '../../static/images/play.svg'
import pause from '../../static/images/pause.svg'
import back from '../../static/images/back.svg'
import next from '../../static/images/next.svg'
import playBig from '../../static/images/play-big.svg'
import pauseBig from '../../static/images/pause-big.svg'
import like from '../../static/images/like-1.svg'
import liking from '../../static/images/like-2.svg'
import random from '../../static/images/random.svg'
import binlyric from '../Tools/lyric'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
class Player extends Component {
    constructor(props){
        super(props)
        this.state = {
            playing:false,
            playmode:random,
            offset: 0,
            showIcon: true
        }
        this.src=''
    }
    controlProgress(e){
        let { duration,currentTime } = this.audio
        var percent = currentTime / duration, perimeter = Math.PI * 2 * 450;
        if(!percent){
            this.progress.setAttribute('stroke-dasharray','0 100')
            return 
        }

        let minutes = Math.floor (currentTime / 60)

        let second = (currentTime % 60).toFixed(0), totalSecond = (duration % 60).toFixed(0)
        second = ( second<10 ? ('0'+second) : second )
        this.currentTime.innerText = minutes+':'+ second
        this.totalTime.innerText = Math.floor(duration/60)+':'+( totalSecond<10 ? ('0'+totalSecond) : totalSecond )

        let width = parseInt(window.getComputedStyle(this.progressbtn.parentNode).width)
        this.progressbtn.style.transform = 'translate('+percent*width+'px,0)'
        this.progressPlayed.style.width = percent*width +'px'

        this.progress.setAttribute('stroke-dasharray',perimeter * percent + " " + perimeter * (1- percent))

        this._roll_lyric(( minutes<10 ? ('0'+minutes) : minutes ),(currentTime % 60))
    }
    changePlay(type,e){
        type==='add'&&this.container.classList.add(styles.play)
        if(type==='move'){
            this.setState({
                showIcon:true,
            },()=>{
                this.container.classList.remove(styles.play)
            })
        }
        e.nativeEvent.stopPropagation()
    }
    togglePlay(e){
        let {playing} = this.state
        if(!this.audio.src){
            return false
        }
        if(this.audio.src !== this.src){
            playing = false
            this.src = this.audio.src
        }

        playing?this.audio.pause():this.audio.play()
        
        this.setState({
            playing: !playing
        })
    }
    toggleIcon(){
        if(this.container.classList.contains(styles.play)){
            this.setState(prevState=>({
                showIcon: !prevState.showIcon
            }))
        }
    }
    componentDidUpdate(){
    }
    componentDidMount(){
    }
    _roll_lyric(minutes,second){
        let offset = this.state.offset
        if(!this.props.song.lyric){
            return
        }
        if(second<10){
            second = `0${second}`
        }
        let curr = (minutes+second).replace(/\./g,'').slice(0,6)
        const time = binlyric.time
        const ln = time.length
        for(let i=offset;i<ln-1;i++){
            let str = time[i].replace(/[\:\.\[\]]/g,'').slice(0,6)
            if(curr>=Number(str-60)){
                this.setState({
                    offset:offset+1
                })
                return
            }
        }
    }
    render(){
        const { playing,playmode,offset,showIcon } = this.state
        const {song} = this.props
        if(song.lyric){
            binlyric.analysis(song.lyric.lrc.lyric)
            
        }
        const likeStorage = JSON.parse(localStorage.getItem('likes')) || {}
        return <div ref={i=>this.container=i} className={song.url?styles.mini:styles.hideminiplay}>
                <div className={styles.background}>
                    <div className={styles.backfilter}></div>
                    <img width="100%" height="100%" className={styles.backimg} src={song.detail&&song.detail[0].al.picUrl} />
                </div>
                {/* bar */}
                <header>
                    <img src={down} onClick={(e)=>this.changePlay('move',e)} />
                    <div className={styles.title}>
                        <h1>{song.detail&&song.detail[0].name}</h1>
                        <h3>{song.detail&&song.detail[0].ar[0].name}</h3>
                    </div>
                </header>

                <div onClick={(e)=>this.changePlay('add',e)}>
                <ReactCSSTransitionGroup
                transitionName="fade"
                transitionEnterTimeout={300}
                transitionLeaveTimeout={300}
                onClick={()=>this.toggleIcon()}
                >
                { showIcon?
                <div key={1} className={styles.icon}>
                    <img className={styles.tran} src={song.detail&&song.detail[0].al.picUrl}/>
                </div>
                :
                <div key={2} className={styles.lyricwrapper}>
                    <ul className={styles.lyric}
                      style={{
                          top: -(offset-1)*1.2+'rem'
                      }}
                      >
                        {binlyric.lyric&&binlyric.lyric.map((i,key)=>(
                            <li key={key} className={key==offset-1?styles.active:''}>{i}</li>
                        ))}
                    </ul>
                </div>
                }
                </ReactCSSTransitionGroup>
                
                {/* info */}
                <div className={styles.info}>
                    <h3>{song.detail&&song.detail[0].name}</h3>
                    <p>{song.detail&&song.detail[0].ar[0].name}</p>
                </div>
                </div>

                {/* control */}
                <div>
                    <div className={styles.minmenucontainer}>
                        <div onClick={()=>this.togglePlay()} className={styles.playbtncontainer}>
                            <svg className={styles.playroundouter} width="32" height="32" viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg"  version="1.1">
                                <circle cx="500" cy="500" r="450" className={styles.outcircle} fill="transparent"/>
                                <circle strokeDasharray="0 100" ref={i=>this.progress=i} className={styles.progresscircle} r="450" cx="500" cy="500" fill="transparent"></circle>
                            </svg>
                            {
                                playing?<img className={styles.pause} src={pause} />:<img className={styles.miniplaybtn} src={play} />
                            }   
                        </div>
                        <img className={styles.minmenu} src={menu} />
                    </div>
                </div>
                <div className={styles.playbottom}>
                    <div className={styles['progress-wrapper']}>
                        <span ref={i=>this.currentTime=i} className={styles.time+' '+styles.timel}>0.00</span>
                        <div className={styles['progress-bar-wrapper']}>
                            <div className={styles['progress-bar']}>
                                <div className={styles['bar-inner']} >
                                    <div ref={i=>this.progressPlayed = i} className={styles['progress-played']}></div>
                                    <div ref={i=>this.progressbtn = i} className={styles['progress-btn-wrapper']}>
                                        <div className={styles['progress-btn']}></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <span ref={i=>this.totalTime=i} className={styles.time+' '+styles.timer}>3.59</span>
                    </div>
                    <div className={styles.control}>
                        <div>
                            <img src={playmode} />
                        </div>
                        <div>
                            <img src={back} />
                        </div>
                        <div onClick={()=>this.togglePlay()}>
                            <img style={{width:'1.5rem',height:'1.5rem'}} src={playing?pauseBig:playBig} />
                        </div>
                        <div>
                            <img src={next} />
                        </div>
                        <div onClick={()=>this.props.toggleLike(song.detail[0])}>
                            <img
                              src={
                                likeStorage[song.detail&&song.detail[0].id]?liking:like
                              } />
                        </div>
                    </div>
                </div>
                <div id="hidden-player">
                    <audio ref={i=>this.audio=i} id="audio" onCanPlay={(e)=>this.togglePlay(e)} onTimeUpdate={e=>this.controlProgress(e)} src={song.url&&song.url[0].url}></audio>
                </div>
            </div>
    }
}

export default Player


