import React, { Component } from 'react'
import styles from './Swipe.scss'
class Swipe extends Component {
    constructor(props){
        super(props)
        this.state = {
            active:1,
            offset:-1
        }
    }
    _canControl(){
        let startX, startY, endX, endY, distance, oldOffset
        this.swipe.ontouchstart = (e) => {
            var touch = e.targetTouches[0];
            startX = touch.pageX;
            startY = touch.pageY;
            oldOffset = this.state.offset
            e.stopPropagation()
        }
        this.swipe.ontouchmove = (e) => {
            this.setTimer('close')

            var touch = e.targetTouches[0], offset = this.state.offset
            endX = touch.pageX;
            endY = touch.pageY;
            distance = startX - endX
            startX = endX
            startY = endY
            offset -= distance/this.props.width

            if(offset<=-(this.props.list.length+2)){
                offset = -1
            }
            this.setState({
                offset: offset
            })
            e.stopPropagation()
        }
        this.swipe.ontouchend = (e) => {
            let num = this.state.active
            if(this.state.offset - oldOffset >1/4){
                num -=1
            }else if(this.state.offset - oldOffset <-1/4){
                num+=1
            }
            if(num<0){
                num=0
            }
            //从尾部回到0
            if(num>=(this.props.list.length+1)){
                setTimeout(()=>{
                    num=1
                    this.container.style.transitionDuration = `0ms`
                    this.setState({
                        offset: -num,
                        active: num
                    },()=>{
                        this.setTimer('CLOSE')
                        this.setTimer('OPEN')
                    })
                },400)
            }else if(num<1){
                setTimeout(()=>{
                    num = this.props.children.length
                    this.container.style.transitionDuration = `0ms`
                    this.setState({
                        offset: -num,
                        active: num
                    },()=>{
                        this.setTimer('CLOSE')
                        this.setTimer('OPEN')
                    })
                },400)
            }

            this.container.style.transitionDuration = `400ms`
            this.setState({
                offset: -num,
                active: num
            })
            setTimeout(() => {
                this.setTimer('open')
                this.container.style.transitionDuration = `0ms`
            }, 400);
            e.stopPropagation()
        }
    }
    setTimer(type){
        if(type==='open'){
            this.timer = setInterval(()=>this.loop(),3000)
        }else{
            clearInterval(this.timer)
        }
    }
    loop(){
        let num = this.state.active, offset = this.state.offset, flag = false
        offset -= 1
        num += 1
        if(offset<=-(this.props.list.length+2)){
            offset = -1
            num = 1
            flag = true
            this.container.style.transitionDuration = `0ms`
        }else{
            this.container.style.transitionDuration = `400ms`
        }
        this.setState({
            offset,
            active:-offset
        },()=>{
            if(flag){
                setTimeout(()=>{
                    this.loop()
                })
            }
        })
        setTimeout(() => {
            this.container.style.transitionDuration = `0ms`
        }, 400);
    }
    componentDidUpdate(prevProps,prevState){
    }
    componentDidMount(){
        this.container.style.transitioProperty = 'transform',
        this.setTimer('open')
        
        this._canControl()
    }
    componentWillUnmount(){
        clearInterval(this.timer)
    }
    render(){
        const { active, offset } = this.state
        const { width,list,children } = this.props
        return (
            <div ref={i=>this.swipe = i} className={styles.wrap}>
                <div ref={i=>this.container = i} className={styles.content}
                  style={{
                      width: width*(list.length+2),
                      transform:`translate(${offset*width}px,0)`
                  }}
                >
                {children[children.length-1]}
                {children}
                {children[0]}
                </div>
                <div className={styles.dot}>
                    {list.map((item,index)=>(
                        <span key={index}
                          className={
                              (index+1==active
                                ||(index==0&&active==list.length+1)
                                ||(index==list.length-1&&active==0)
                                )?styles.active:''}></span>
                    ))}
                </div>
            </div>
        )
    }
}

export default Swipe


