import React, { Component } from 'react'
import styles from './index.scss'
class List extends Component {
    constructor(props){
        super(props)
    }
    handleClick(id){
        this.props.getSong(id)
        document.getElementById('audio').play()
    }
    render(){
        const likes = JSON.parse(localStorage.getItem('likes')) || {}
        const arr = Object.keys(likes)
        return (
            <div className={styles.like} style={this.props.style}>
                <div></div>
                <ul>
                    {
                        arr.map((item,index) => (
                            <li key={index} onClick={()=>this.handleClick(likes[item].id)}>
                                <span>{index+1}</span>
                                <div>
                                    <p className={styles.title}>{likes[item].name}</p>
                                    <p className={styles.desc}>{likes[item].artists?likes[item].artists[0].name:likes[item].ar[0].name}</p>
                                </div>
                            </li>
                        ))
                    }
                </ul>
            </div>
        )
    }
}

export default List


